import React from "react"
import '../css/App.css';
import '../css/ProblemPage.css';


export default function SelectLable(props) {
    const {lazyCheckList, checkList, answer, handleChange, text, id, value} = props


    let cls = "p-label"
    if(!lazyCheckList) {
        cls += ""
    }else if(lazyCheckList[id] === ""){
        cls += ""
    }else if(answer[id] === value) {
        cls += " p-correct"
    }else if((lazyCheckList[id] === answer[id]) && lazyCheckList[id] === value){
        cls += " p-correct"
    }else if(lazyCheckList[id] !== answer[id] && lazyCheckList[id] === value){
        cls += " p-wrong fadeout"
    }else {
        cls += " fadeout"
    }

    return (
        <div className={cls}>
            <label className="radio-container">
                <input
                type="radio" 
                name={id}
                className="radio-input"
                value={value}
                checked={checkList[id] === value}
                onChange={handleChange}
                // required
                disabled={lazyCheckList!==undefined}
                    />
                <div className="selection-raido-txt" style={{display:"inline"}}dangerouslySetInnerHTML={{__html: text}} />
            </label>
        </div>
    )
}