import React from "react"
import SelectLable from "./SelectLabel";
import check_wrong_path from '../images/check_wrong.png';
import check_green_path from '../images/check_green.png';
import check_green_light_path from '../images/check_green2.png';
import check_gray_path from '../images/check_gray.png';
import back_on_path from '../images/back_on.png';

import { Link } from "react-router-dom";
import { useReward } from 'react-rewards';

import { c1 } from "./problems/c1";
import { c2 } from "./problems/c2";
import { c3 } from "./problems/c3";
import { c4 } from "./problems/c4";
import { c5 } from "./problems/c5";
import { c6 } from "./problems/c6";
import { c7 } from "./problems/c7";
import { c8 } from "./problems/c8";
import { c9 } from "./problems/c9";
import { c10 } from "./problems/c10";

import '../css/App.css';
import '../css/ProblemPage.css';
import Footer from "./Footer";

const all_problems = [c1, c2, c3, c4, c5, c6, c7, c8, c9, c10]

export default function ProblemPage(props) {
    
    const chapter = props.chapter

    const p_base = all_problems[chapter-1]
    const p_length = Object.keys(p_base).length
    const answer = {}
    const checkListInit = {}

    const chapterNum = 10
    const title = props.title
    const isLastChapter = chapter === chapterNum

    const [problemClearList, setProblemClearList] = React.useState(()=>{
        const initClearList = new Array();
        for(let i=0 ;i <p_length; i++){
            initClearList.push(false)
        }
        return JSON.parse(localStorage.getItem(`book4-chapter${chapter}-clears`)) || initClearList})

    let isAllClear = true
    for(let i=0; i <problemClearList.length; i++){
        if(!problemClearList[i]){
            isAllClear = false
            break
        }
    }

    const [isClear, setIsClear] = React.useState(false)

    const [isEnd, setIsEnd] = React.useState(false)
    const [correctCnt, setCorrectCnt] = React.useState(0)
    const [checkList, setCheckList] = React.useState(checkListInit)
    const [lazyCheckList, setLazyCheckList] = React.useState(undefined)

    const { reward, isAnimating } = useReward('rewardId', 'confetti', {
        // lifetime: 300,
        position: "absolute"
    });

    function checkClear(){
        let allCorrect = true
        let cnt = 0
        const yourCheckList = new Array()

        for(let i=0; i<p_length; i++){
            const key = `p${i+1}`

            let isCorrect = false
            if(checkList[key] === answer[key]){
                cnt += 1
                isCorrect = true
            }else{
                allCorrect = false
            }

            // 過去に正解した場合はlocalstorageは正解のままに
            if(problemClearList[i]){
                isCorrect = true
            }
            yourCheckList.push(isCorrect)
        }

        setProblemClearList(yourCheckList)
        localStorage.setItem(`book4-chapter${chapter}-clears`, JSON.stringify(yourCheckList))

        setCorrectCnt(cnt)
        if(allCorrect) {
            setIsClear(true)
        }

    }

    function onSubmit(event) {
        event.preventDefault();
        setLazyCheckList(checkList)
        setIsEnd(true)
        checkClear()
      }

    function handleChange(event) {
        const {name, value} = event.target
        setCheckList(oldData => {
            return {...oldData,
                [name]: value
            }
        })
    }

    function reset(){
        setIsEnd(false)
        setLazyCheckList(undefined)
        setCheckList(checkListInit)
    }

    React.useEffect(() => {
        if (isClear) {
            // reward()
            setTimeout(reward, 500)
            setTimeout(reward, 1500)
        }
    }, [isClear]);


    function SeleclLabels(){ 
        const problemDomList = []

        for(let i=1; i < p_length+1; i ++){
            const p_key = `p${i}`
            const q = p_base[p_key]

            const selection_list = q.selection
            answer[p_key] = q.answer
            checkListInit[p_key] = ""

            const selectionDom = selection_list.map((value, index)=>{
                return <SelectLable 
                    checkList={checkList}
                    lazyCheckList={lazyCheckList}
                    answer={answer}
                    handleChange={handleChange}
                    text={value}
                    id={p_key}
                    key={`${p_key}-${i}-${index}`}
                    value={String(index+1)}
                />
            })

            let container_cls = "p-q-container"
            if(lazyCheckList && lazyCheckList[p_key] === ""){
                container_cls += " fadeout_super"
            }

            const problemDom = <div id={`Q${i}`} key={`${p_key}-${i}`} className={container_cls}>
                    <div className="p-q-title-container">
                        <div className="p-header-container">
                            {!isEnd && <img src={problemClearList[i-1] ?  check_green_path : check_gray_path} className="check_top-icon" alt="correct icon" />}

                            <h3 className="p-question-title">問題 {i}</h3>
                        </div>
                        {isEnd &&
                            <img src={checkList[`p${i}`] === answer[`p${i}`] ? check_green_light_path : check_wrong_path} className="check_icon_small" alt="correct icon"/>
                        }
                    </div>
                    <p className="p-question-text">
                        <span dangerouslySetInnerHTML={{__html: q.question}} />
                    </p>
                    {q.img && <img className="p-img" src={require(`../images/problems/${q.img}`)} />}
                    {selectionDom}
                </div>

            problemDomList.push(problemDom)
        }
        return problemDomList
    }
// WRONG
    return (
        <div className="p-container">
            {/* =====  PROBLEM HEADER ===== */}
            <div className="problem-title">
                <div className="problem-title-left">
                    {/* {isAllClear || isClear && <img src="check_green_path" className="top-icon" alt="correct icon" 
                    />} */}
                    <h2 className="p-title">{title}</h2>
                </div>
                <Link to={'/'}>
                    <img src={back_on_path} className="top-btn"/>
            </Link>
            </div>
           
            {/* =========================== */}

            <form onSubmit={onSubmit}>
                <SeleclLabels />
                <div className="btn-container">
                    {!isEnd && <button className="btn" id="submit-btn">答え合わせ</button>}
                </div>

                <div className="result-container">
                    {!isEnd && <span id="rewardId" />}
                    {isEnd && <h2 id="rewardId" className="result-txt">
                        <span className="small-result-txt">点数</span>
                        <span className="result-num">{correctCnt}</span>
                        <span className="small-result-txt">{`/ ${p_length}`}</span>
                        {isClear && <span className="reward-btn" onClick={reward}> 🎉</span>}
                    </h2>}
                    
                    {(isEnd && isClear && isLastChapter) && 
                        <Link to={`/`}>
                        <button className="btn result-btn">目次へ</button>
                        </Link>
                    }
                    {(isEnd && isClear && !isLastChapter) && 
                        <Link to={`/chapter${chapter + 1}/`}>
                        <button className="btn result-btn">次の問題へ</button>
                        </Link>
                    }

                    {(isEnd && !isClear) && <button onClick={reset} className="btn result-btn">やり直す</button>}
                    
                </div>
            </form>
            <Footer />
        </div>
    )
}