import React from "react"
import cover_path from '../images/cover.jpg';
import ChapterButton from "./ChapterButton";
import Footer from "./Footer";
import '../css/TopPage.css';


export default function TopPage(props) {
    const chapterNum = 10
    
    const titleList = props.titleList.data

    const [userLog, _] = React.useState(
        () => {
            const _userLog = {
                clearCnt:0,
                clearList:[]
            }
            let sumCorrect = 0
    
            for(let i=0; i <chapterNum; i++){
                const _clearList = JSON.parse(localStorage.getItem(`book4-chapter${i+1}-clears`)) || [false]
                
                let _allCorrect = true
                for(let k=0; k < _clearList.length; k++){
                    if (_clearList[k]) {
                        sumCorrect += 1
                    }else{
                        _allCorrect = false
                    }
                }

                _userLog.clearList.push(_allCorrect)
            }

            _userLog.clearCnt = sumCorrect
            return _userLog
        }
    )

    const chapterButtonDom = []
    for(let i=0; i <chapterNum; i++){
        chapterButtonDom.push(
        <ChapterButton key={i} isClear={userLog.clearList[i]} value={i+1} title={titleList[i]} clickHandler={props.clickHandler}/>
        )
    }

    return (
        <div className="main-container">
            <div className="top-container">
                <div className="top-txt-container">
                    <a href="https://www.amazon.co.jp/dp/4873119758/ref=cm_sw_r_tw_dp_7E324ZWVC18MSY0DRJ1G" target="_blank">
                        <img src={cover_path} className="cover-img" alt="book cover" />
                    </a>
                    <h2 className="top-title">強化学習100題</h2>
                    <p className="top-subtitle">『ゼロから作るDeep Learning ❹ ―強化学習編』</p>
                    <p className="top-subtitle2">確認テスト</p>
                    
                    <div className="top-clear-cnt-container">
                    <h2 style={{background: `linear-gradient(90deg,rgb(133, 253, 133) 0%,rgb(198, 254, 198) ${userLog.clearCnt}%, white ${userLog.clearCnt}%,white 100%)`}} className="top-clear-cnt">
                        <span className="small-result-txt">正解数</span>
                        <span className="result-top-num">{userLog.clearCnt}</span>
                        <span className="small-result-txt">{`/ 100題`}</span>
                        {/* {isClear && <span className="reward-btn" onClick={reward}> 🎉</span> */}
                    </h2>
                    </div>


                    
                </div> 
                <div className="top-chapter-container">
                        {chapterButtonDom }
                </div>

                
            </div>
            <Footer />
        </div>
    )
}